import React, {useEffect} from 'react'
import style from './EnterNewPassword.module.css'
import {useDispatch, useSelector} from "react-redux";
import {AppStoreType} from "../../bll/store";
import {useFormik} from "formik";
import {setNewPassword} from "../../bll/enterNewPasswordReducer";
import {Redirect, useParams} from "react-router-dom";

type FormikErrorType = {
    password?: string
}

const EnterNewPassword = () => {

    let completed = useSelector<AppStoreType, boolean>(state => state.enterNewPassword.isRequestSuccess)
    let buttonDisabled = useSelector<AppStoreType, boolean>(state => state.enterNewPassword.isButtonDisabled)
    const {token} = useParams<{ token: string }>()
    const dispatch = useDispatch()

    const formik = useFormik({
        initialValues: {
            password: '',
            confirmPassword: ''
        },
        validate: (values) => {
            const errors: FormikErrorType = {};
            if (!values.password) {
                errors.password = 'ПУСТО';
            } else if (values.password.length < 4) {
                errors.password = 'БОЛЬШЕ ТРЕХ СИМВОЛОВ ДОЛЖНО БЫТЬ';
            }

            // if (values.password !== values.confirmPassword) {
            //     errors.confirmPassword = 'ПАРОЛИ НЕ СОВПАДАЮТ'
            // }
            return errors;
        },
        onSubmit: (values) => {
            dispatch(setNewPassword(values.password, token))
        }
    })

    if (completed) {
        return (
            <Redirect to={'/login'}/>
        )
    }

    return (
        <div>
            <form onSubmit={formik.handleSubmit}>
                <input id={"password"} name={"password"} type={"password"} placeholder={"Password"}
                       onChange={formik.handleChange} onBlur={formik.handleBlur}
                       value={formik.values.password}/>
                {formik.errors.password && formik.touched.password ? <div>{formik.errors.password}</div> : null}

                <button type={"submit"} disabled={buttonDisabled}>Send</button>

            </form>


        </div>
    )
}
export default EnterNewPassword

